import React, { Suspense } from "react";
import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import { embedTag } from "../../../../actions/api";
import { isInstaUser } from "../../../../utils";
const Network = React.lazy(() => import("../../Elements/Network/Network"));
const AuthorDetails = ({ Post, CardStyle }) => {
  const { author, networkId } = Post
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const instaUser =  isInstaUser(author.name);
  const postAuthor = (CardStyle.author.status && !instaUser) ? true : false;
  const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !instaUser && username != '') ? true : false;

  return (
    <div className={`tb_hs_author_wrapper`} aria-label="Author details" aria-roledescription="post author details">
      <div className="tb_hs_author">

        {postAuthor ? <Author author={author} network={Post.network} networkId={networkId} authorClass={'tb_hs_author_profile'} />: ''}
        <div className="tb_hs_author_info">
          {postAuthor ? <div className="tb_hs_authorname">{author.name}</div> : ''}
          <div className="tb_hs_post_info">
            {postAuthor ? <div className="tb_hs_username">{`@${username}`}</div> : ''}
            {authorSeprator ? <div className="tb_hs_seprator"> </div> : ''}
            {CardStyle.timeStatus ? <Time createdAt={Post.createdAt} timeClass={`tb_hs_time tb-cTBfont-${CardStyle.author.variant}`} /> : ''}
          </div>
        </div>
      </div>
      {!embedTag ?
        <div className="tb_hs_social_">
          <Suspense fallback={<></>}><Network networkClass={'tb_hs_social__ico'} networkId={networkId} iconStyle={CardStyle.icon} /></Suspense>
        </div>
        : null
      }
    </div>
  );
}
export default AuthorDetails