import React, { PureComponent } from "react";
import { gtmAnalytics, themePostTracking } from "../../../../actions/moredataAction";
import './scss/ShareOnHover.scss'

export default class ShareOnHover extends PureComponent {
  onClickData = (shareURL) => event => {
    const { Post, ThemeInfo } = this.props;
    event.stopPropagation();
      themePostTracking({
      type: 2,
      action: 2,
      wall: ThemeInfo.wallID,
      feed: Post.feedId,
      post: Post.referenceId ? Post.referenceId : Post.id,
      owner: ThemeInfo.ownerId
    })
    gtmAnalytics("Tagbox", "shareUrl", "shareUrl", shareURL)
    window.open(shareURL, "_blank")
  }

  render() {
    const { shareClass, Post } = this.props;
    const {facebook,twitter, linkedin } = Post?.share;
    
    const GetShareIncon=({shareOn, icon})=>{
      return <div className="tb_share_icon_list">
            <div onClick={this.onClickData(shareOn)} className={`tb_share_ico__ tb__icon tb-${icon}`} title={icon}><div></div></div>
      </div>
    }
    return <div className={`tb_share_wrapper ${shareClass}`}>
      <div className={`tb_share_button_ tb__icon ${'tb-share'}`}><div></div></div>
      <div className="tb_share_icon_list_wrap">
        {facebook ? <GetShareIncon shareOn={facebook} icon={'facebook'} />  : ''}
        {twitter  ? <GetShareIncon shareOn={twitter} icon={'twitter'} />  : ''}
        {linkedin ? <GetShareIncon shareOn={linkedin} icon={'linkedin'} />  : ''}
      </div>
    </div>
  }
}
