import React, { PureComponent, Suspense } from "react";
import { connect } from "react-redux";
import { Splide } from '@splidejs/react-splide';
import '../scss/ThemesSplide.scss'
import './scss/HorizontalSlider.scss'
import { isSocialEmbed, isSocialWall, isTagShop } from "../../../actions/api"
import { detectMobile } from '../../../utils'
import { nextData } from "../../../actions/moredataAction";
import Card from "./Elements/Card";

const DivWrapper = ({ isTrue, children }) => {
  return isTrue ? <div style={{ display: 'flex', justifyContent: "center" }}>{children}</div> : children
}
class HorizontalSlider extends PureComponent {
  constructor(props) {
    super(props)
    this.state = { parent_id: '', postData: [], loadData : false }
    this.inputRefs = {}
    this.isDragging = false; // Class property to track dragging
  }
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole && classNameRole.forEach((element) => {element.removeAttribute("aria-hidden");element.removeAttribute("role")});
    }, 100);

    const { Post } = this.props
    this.setState({ Post })
    window.addEventListener('keydown', this.handleKeyPress);
  }

  handleKeyPress = event => {
    try {
      const { ThemeStyle } = this.props
      if (ThemeStyle.slide.autoSlide == 1) {
        const isInput = ['INPUT', 'TEXTAREA'].includes(event.target.tagName);
        if (!isInput && event.code === 'Space') {
          const { parentID } = this.props
          event.preventDefault(); // Prevent scrolling the page
          var Autoplay = this.inputRefs[parentID].splide.Components.Autoplay;
          if (Autoplay.isPaused()) Autoplay.play();
          else Autoplay.pause();
        }
      }
    }
    catch (error) {
    }
  }
  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyPress);
  }
  requestData = () => {
    const { appendData, preRender, hasMoreData, nextData, ThemeStyle, ThemeInfo } = this.props;
     !this.state.loadData && this.updatepost(0) 

    if (hasMoreData) {
      nextData(
        isTagShop ? ThemeInfo.webId : ThemeInfo.wallId,
        Math.floor(Date.now() / 1000),
        ThemeStyle.totalPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  autoScrollSlider = (splide) => {
    const { ThemeStyle } = this.props
    const autoScrollStatus = ThemeStyle.autoScrollStatus;

    splide.on("drag", () =>{  
      this.isDragging = true; // Reset flag after move
      !this.state.loadData && this.updatepost(0)
   } );

    splide.on("move", () => { !this.state.loadData && !this.isDragging && this.updatepost(3)} );

    if (autoScrollStatus == 1 || isTagShop) {
      const currentIndex = splide.index
      const slideLength = splide.length
      const permove = splide["_o"].perPage
      if (slideLength - currentIndex === permove) this.requestData()
    }
  }

  
  updatepost = (e) => {    
    if(!this.state.loadData){ 
      this.setState({ loadData : true })
       setTimeout(() => this.props.loadMoredata() , (e*1000))
    }
}

  render() {
    const { postData, completeDataObject, CardStyle, ThemeStyle, ProductSetting, ThemeInfo, clickToShowPopUp, onClickToCTA, postSizeInPx, parentID, loaderData, onClickPopUpSlider, adjustWidth, cardNumber } = this.props;
    const { ownerId } = ThemeInfo
    const inputName = parentID;
    var newPostdata = []
    const numberOfCoumn = ThemeStyle.column.desktop;
    const mobileColumn = ThemeStyle.column.mobile;
    const slidePost = ThemeStyle.slide.autoSlide;
    const slideDuration = ThemeStyle.slide.slideDuration;
    const autoScrollStatus = ThemeStyle.autoScrollStatus;
    const rowCount = !ThemeStyle.row || ThemeStyle.row === 0 ? 1 : (postData.length < 15 && (isSocialWall || isSocialEmbed)) ? 1 : ThemeStyle.row;

    let isVisible = true
    const cloneStatus = (ThemeInfo.productType == 9 || isTagShop) && (ownerId == 142546 || ownerId == 141171 || ThemeStyle.slide.clonePost !== undefined) ? (ownerId == 142546 || ownerId == 141171 || ThemeStyle.slide.clonePost === 0) ? { clones: 1 } : {} : {};


    var i = 0, n = postData.length;
    while (i < n) { newPostdata.push(postData.slice(i, i += rowCount)) }


    if (isTagShop && cloneStatus && Object.keys(cloneStatus).length == 0) {
      if (ThemeInfo.wallId == 16677) {
        newPostdata = newPostdata
        if (window.dataFilterType === 'product') isVisible = newPostdata && newPostdata.length >= 5 ? true : false
      }
      else newPostdata = newPostdata.length < 12 ? Array(15 - newPostdata.length).fill(newPostdata).flat() : newPostdata;
    }

    const isType = autoScrollStatus != 1 ? Object.keys(cloneStatus).length ? false : (ThemeInfo.productType == 9 || 'loop') : (isTagShop ? 'slide' : 'loop')

    const isCenter = (ownerId == 142546 || ownerId == 141171) && (numberOfCoumn == 0 || mobileColumn == 0) && !detectMobile() ? parseInt(cardNumber) < postData.length ? false : true : false
    const postLength = detectMobile() ? 1 : postData.length
    const settings = {
      ...cloneStatus,
      type: autoScrollStatus === 1 ? false : isType,
      rewind: false,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : ThemeInfo.productType == 9  ? 3 : postLength > 6 ? 6 : postLength,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      updateOnMove: true,
      pagination: false,
      arrows: true,
      breakpoints: {
      560: { perPage: Math.max(mobileColumn || 1, 1) },
      767: { perPage: Math.max(mobileColumn || 2, 2) },
      991: { perPage: mobileColumn || Math.min(postLength, 3) },
      1200: { perPage: numberOfCoumn || Math.min(postLength, 4) },
      1400: { perPage: numberOfCoumn || Math.min(postLength, 5) },
      1600: { 
        perPage: numberOfCoumn ||  Math.min(postLength, 6)
      }
    }
  }
    return (<>{parentID != "" ? <DivWrapper isTrue={isCenter}> <div className="tb_hs_post_container" style={isCenter ? { maxWidth: postData.length * postSizeInPx.adjustWidth } : null}>
      {isVisible ? <Splide  hasTrack={false} className="tb_hs_post_slider" options={settings} ref={(parentID) => this.inputRefs[inputName] = parentID} onMove={this.autoScrollSlider}>
        <div className="splide__track">
          <div className="splide__list">
              {
                (ThemeStyle.row > 1 && newPostdata.length !== 1 && newPostdata.length % 2 !== 0 ? newPostdata.pop() : newPostdata) && ((newPostdata && newPostdata.length > 0)) && newPostdata.map((item, index) => {
                  return <div key={`tb_${index}_${index}`} className={`splide__slide splide__${index}`}>
                    <div className="tb_hs_col_wrap">
                      {item.map((subItem) => {
                          const cardData = completeDataObject[subItem];
                          return <Card  Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} ThemeInfo={ThemeInfo} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} adjustWidth={adjustWidth} isTrue={isCenter} />
                        })
                      }
                    </div>
                  </div>
                })}
            </div>
          </div>
        <div className="splide__arrows splide__arrows--ltr tb_hs_arrow_wrapper_">
          <div className="tb_hs_arrow splide__arrow splide__arrow--prev tb_hs_arrow tb_hs_arrow_left__ tb__icon tb-arrow-left-alt" role="button" aria-label="Go to previous slide" ><div></div></div>
          <div className={`tb_hs_arrow splide__arrow splide__arrow--next  tb_hs_arrow_right__ tb__icon tb-arrow-right-alt ${loaderData.isShowMoreLoading ? `tb_a_spinner` : ``}`} onClick={isTagShop  ? event => this.requestData() : null} role="button" aria-label="Go to next slide" ><div></div></div>
        </div>
      </Splide> : null}
    </div> </DivWrapper> : null}</>)
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    nextData: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(nextData(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(HorizontalSlider);