import React, { PureComponent, Suspense } from "react";
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from "../../../../utils/Network";
import "./IconDefaultColor.scss"
const DefaultNetwork = React.lazy(() => import("./DefaultNetwork"));

export default class Network extends PureComponent {
  render() {
    const { networkId, networkClass, iconStyle, isPopUp } = this.props;
    const defaultNetworks = [7, 4, 36]
    const isDefaultIcon = iconStyle?.default || (defaultNetworks.includes(networkId))
    return (isDefaultIcon ?  <Suspense fallback={<></>}><DefaultNetwork networkId={networkId} networkClass={networkClass} /></Suspense> : <div className={`${networkClass} tb__icon tb-${GET_NETWORK_ICON(networkId)}`} aria-label={`post network ${GET_NETWORK_NAME(networkId)}`}><div></div></div>);
  }
}
